<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <ctitle :title="'受信人名单'"></ctitle>
        <!--<span class="c-sxr">受信人：{{this.curName}}</span>-->
        <div class="w-echart" id="w-echart">
          <div ref="barEl" style="height:100%;">
            <horizontalBar :params="leftBarParams" @departmentClick="changeType"></horizontalBar>
          </div>
        </div>
      </div>
      <div class="w-right">
        <div class="c-top">
          <div class="ct-left">
            <ctitle :title="'来信信访量信访初信重信联名信走势'"></ctitle>
            <div class="chart">
              <LinesR :params="zsLineParams"></LinesR>
            </div>
          </div>
          <div class="ct-right">
            <ctitle :title="'来信信访量信访初信重信联名信占比'"></ctitle>
            <div class="inner">
              <loop-pie class="chart" :params="zbPieParams"></loop-pie>
              <ring class="chart" :params="zbRingParams"></ring>
            </div>
          </div>
        </div>
        <div class="c-bottom">
          <div class="cb-left">
            <ctitle :title="'信访群体'"></ctitle>
            <div class="inner">
              <div class="chart">
                <loopPie :params="dqfbPieParams"></loopPie>
                <p class="chartTitle">地区分布</p>
              </div>
              <div class="chart">
                <barline :params="nlfbBarParams"></barline>
                <p class="chartTitle">年龄统计</p>
              </div>
            </div>
          </div>
          <div class="cb-right">
            <ctitle :title="'信访诉求'"></ctitle>
            <div class="inner">
              <div class="chart">
                <pie-chart3D :params="xfmdPieParams"></pie-chart3D>
                <p class="chartTitle">信访目的占比</p>
              </div>
              <div class="chart">
                <loopPie :params="tswtPieParams"></loopPie>
                <p class="chartTitle">投诉问题占比</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import barline from "@/components/bar/BarLine";
  import LinesR from "@/components/line/LinesR";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import BScroll from "better-scroll";
  import horizontalBar from "@/components/bar/HorizontalBar";
  import ring from "@/components/pie/ring";

  export default {
    name: "sxr",
    components: {
      ctitle,
      barline,
      LinesR,
      loopPie,
      PieChart3D,
      horizontalBar,
      ring
    },
    data() {
      return {
        loadFlag: false,
        peopleId: "",
        curName: "",
        listScroll: null,
        // 左侧柱图
        leftBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true,
          }
        },
        // 信访量走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            left: "5%",
            right: "6%",
            top: "15%",
            bottom: "10%",
            legendTop: "0%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        },
        // 信访量占比
        zbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.gradientColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
            colorsArr: ["#13f0ff", "#4eed99", "#d93e75", "#ccbe51", "#eb9002", "#1ba1ff", "#563ad2", "#f89e67"]
          }
        },
        zbRingParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 200,
            radius: ['54%', '65%'],
            bgRadius: ['59%', '60%'],
            center: ["50%", "50%"],
            colors: [
              ["#fee89f", "#eea570"]
            ]
          }
        },
        // 地区分布
        dqfbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.pieMoreColors,
            title: "",
            radius: ["45%", "60%"],
            center: ["50%", "50%"],
            centerRadius: ["35%", "36%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            borderColor: "#0B2244",
            bRichPadding: [30, -50, 0, -50],
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "20%", // label字号
            rollFlag: true
          }
        },
        // 年龄分布
        nlfbBarParams: {
          data: [],
          canvasStyle: {
            width: "110%",
            height: "100%"
          },
          options: {
            title: "年龄占比",
            xName: "name", //x轴字段
            axisValueShow: true,
            splitLineShow: false, //value轴splitline是否显示
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "5%",
            top: "15%",
            bottom: "15%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            axisLabelColor: "#82d8ff",
            itemLabelColor: "#daf3ff",
            colors: {
              bar: [
                ["#d2f69d", "#79cc7a"]
              ]
            },
            map: {
              value: "人数"
            }
          }
        },
        // 信访目的
        xfmdPieParams: {
          data: [],
          angle: 20,
          radius: "30%",
          labelRadius: 25,
          fontSize: this.fontSize(0.16),
          color: this.$STANDARD.pieColors,
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 投诉问题
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            title: "",
            radius: ["40%", "50%"],
            center: ["50%", "50%"],
            centerRadius: ["35%", "36%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            borderColor: "#0B2244",
            bRichPadding: [30, -50, 0, -50],
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "20%", // label字号
            threeLevel: true,
            rollFlag: true
          }
        },
      };
    },
    mounted() {
      this.getLeftBarApi(this.date);
    },
    methods: {
      getLeftBarApi(date) {
        // 1.受信人名单（左侧柱图）
        get_http("xfxsLxSxrLeftbar", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            let data = res.data;
            this.startData(data, date);
          })
          .catch(err => {
            console.log(err);
          });
      },
      startData(data, date) {
        this.leftBarParams.data = data.data.sort((a, b) => b.value - a.value);
        this.curName = this.leftBarParams.data[0].name;
        this.getRightApi(this.curName, date);
        this.computation();
      },
      getRightApi(name, date) {
        // 2.信访量走势（折线）
        get_http("xfxsLxSxrZsLine", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zsLineParams.map = res.data.map;
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.信访量占比（饼图）
        get_http("xfxsLxSxrZbPie", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.信访量占比（圆环图）
        get_http("xfxsLxSxrZbRing", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.zbRingParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.地区分布
        get_http("xfxsLxSxrDqfbPie", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.dqfbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6.年龄分布
        get_http("xfxsLxSxrNlfbBar", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.nlfbBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7.信访目的
        get_http("xfxsLxSxrXfmdPie", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.xfmdPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 8.投诉问题
        get_http("xfxsLxSxrTswtPie", {day:`[${date.startDate},${date.endDate}]`})
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      changeType(name) {
        // this.leftBarParams.data.filter((item, index) => {
        //   if (item.name === res.params.name) {
        //     this.curName = item.name;
        //   }
        // });
        this.curName = (name === '' ? 'drop' : name);
        this.getRightApi(this.date,this.curName);
      },
      computation(data) {
        let height = (60 * this.leftBarParams.data.length) / 100 + "rem";
        $(this.$refs.barEl).css('height', height);
        this.initScroll();
      },
      // 初始化滚动条
      initScroll() {
        let listwraper = document.getElementById("w-echart");
        this.scrollFunc(this.listScroll, listwraper);

        // 更改滚动条样式
        $(".bscroll-vertical-scrollbar").css({
          width: ".1rem",
          background: "rgba(2, 144, 213, .2)"
        });
        $(".bscroll-vertical-scrollbar .bscroll-indicator").css({
          backgroundImage:
            "linear-gradient(to bottom, rgba(14, 149, 210, 0.8) , rgba(19, 240, 255, 0.8))",
          border: "none"
        });
        $(".bscroll-horizontal-scrollbar .bscroll-indicator").css({
          backgroundImage:
            "linear-gradient(to right, rgba(14, 149, 210, 0.8) , rgba(19, 240, 255, 0.8))",
          border: "none"
        });
      },
      scrollFunc(scrollName, scrollWrap) {
        if (!scrollName) {
          scrollName = new BScroll(scrollWrap, {
            bounce: false,
            momentumLimitDistance: 5,
            scrollX: true,
            scrollbar: {
              fade: false,
              interactive: false // 1.8.0 新增
            },
            mouseWheel: true
          });
        } else {
          // 复用Bscroll
          scrollName.refresh();
        }
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getLeftBarApi(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .w-left
        width 3rem
        height 100%
        position relative
        font-size 0
        .c-sxr
          margin-left .4rem
          line-height .3rem
          font-size .16rem
          color #fff
          text-shadow 0 0 10px #276bf4
        .w-echart
          position relative
          width 100%
          height 90%
          overflow hidden

      .w-right
        display flex
        flex 1
        flex-direction column
        box-sizing border-box
        .c-top
          flex 1
          display flex
          position relative

          .ct-left
            flex 1
            height 100%
            position relative

            .chart
              width 100%
              height 90%
              position relative
              top 0.2rem

          .ct-right
            flex 1
            height 100%
            .inner
              display flex
              height calc(100% - 0.28rem)
              .chart
                flex 1
                width 100%
                height 100%
        .c-bottom
          flex 1
          position relative
          display flex
          .chartTitle
            position absolute
            width auto
            bottom 0.1rem
            left 50%
            color #fff
            font-weight bold
            font-size 0.2rem
            transform translate(-50%, 0)
            font-family '微软雅黑'
          .cb-left
            flex 1
            height 100%
            position relative
            width 100%
            box-sizing border-box
            .inner
              display flex
              height 100%
              .chart
                flex 1
                width 100%
                height 90%
                position relative

          .cb-right
            flex 1
            width 100%
            height 100%
            position relative
            box-sizing border-box
            .inner
              display flex
              height 100%
              .chart
                flex 1
                width 100%
                height 90%
                position relative

</style>
